@import url(https://fonts.googleapis.com/css2?family=Coming+Soon&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Coming Soon",cursive}.app{display:flex;flex-direction:column}
.sidebar{text-align:center;background:url("https://miro.medium.com/max/1400/1*Pe46dEFOgqI-RMOIsSKxcQ.jpeg");background-size:contain;border-top:5px dashed #000;border-left:5px dashed #000;border-right:5px dashed #000}.sidebar__logo img{background:#fff;width:60%}.sidebar__search{padding:0 20px 10px;width:100%;background:rgba(19,19,19,.9)}.sidebar__searchbox{padding:10px}.sidebar__filters{padding:10px}@media screen and (min-width: 1025px){.sidebar{background:url("https://miro.medium.com/max/1400/1*Pe46dEFOgqI-RMOIsSKxcQ.jpeg");width:100%;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.sidebar__logo{background:#fff;border-left:7px dashed #000;border-right:7px dashed #000;width:40%}.sidebar__logo img{width:50%}.sidebar__search{width:40%;background:rgba(19,19,19,.9)}.sidebar__heading{color:#f5f5f5}}
.search-container__input{width:100%;font-size:18px;border:none;border-radius:4px;padding:10px;color:#000}.search-container__input::-webkit-input-placeholder{color:#757575;padding-left:5px}.search-container__input:-ms-input-placeholder{color:#757575;padding-left:5px}.search-container__input::placeholder{color:#757575;padding-left:5px}
.filters{text-align:left;display:flex;flex-direction:column;align-items:center;padding:5px;background-color:#fff;border-radius:10px;border:5px solid gray}
.wrapper__checkbox{margin-right:5px}
.main{text-align:center;background-color:#131313;width:100%;height:100%}.main__heading{color:#fff;padding:20px;letter-spacing:2px;font-size:24px}
.beer-gallery{justify-content:center;display:flex;flex-wrap:wrap;grid-gap:20px;gap:20px;padding:10px;background:url("https://i.pinimg.com/originals/5b/7c/f3/5b7cf3a67ac589c4c496460fb39afed9.jpg");background-size:contain}
.card{background-color:transparent;width:250px;height:400px;-webkit-perspective:1000px;perspective:1000px}.card__inner{position:relative;width:100%;height:100%;text-align:center;transition:-webkit-transform .8s;transition:transform .8s;transition:transform .8s, -webkit-transform .8s;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.card__front{background:rgba(216,216,216,.9);border:4px solid #000;border-radius:50px 0;padding:20px}.card__front-info{border-radius:15px;width:100%;padding:10px;background-color:#fff}.card__front-extra{margin-top:5px;font-size:14px;display:flex;justify-content:space-around}.card__front-extra li{display:block}.card__front-extra span{font-weight:bold}.card__front-img{height:220px;padding:10px;-webkit-transform:rotate(20deg);transform:rotate(20deg)}.card__back{background-color:#222325;color:#fff;-webkit-transform:rotateY(180deg);transform:rotateY(180deg);border-radius:0 50px;padding:20px;display:flex;flex-direction:column;justify-content:space-around;border:4px solid #f3f3f3}.card__back-heading{color:#fff}.card__back-tagline{font-style:italic;color:#fdfdfd;text-decoration:underline}.card__back-description{font-size:12px;padding:5px;color:#fff}.card__front,.card__back{position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden}.card.card:hover .card__inner{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}
